













import { Component, Vue, Prop } from "vue-property-decorator";
import { CategoryModule } from "@/store/modules/category";
@Component({})
export default class VerticalNavLink extends Vue {
  @Prop({ default: {} })
  private item!: any;
  // select menu
  selectedMenu() {
    CategoryModule.selectedCategory(this.item.id);
  }
}
